<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <qrcode :floornum="encryption"></qrcode>
      <div class="floortext">
          <span>*{{languages.a25[langIndex]}}*</span>
      </div>
      <van-button @click="back()" class="back_button" color="#7e7e7e" round type="info">{{languages.a6[langIndex]}}</van-button>
    </main>
  </div>
</template>

<style scoped>
main{
    margin-top: 3.2rem;
    margin-bottom: 2.5rem;
    width: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.floortext{
    padding: .4rem;
    font-size: .22rem;
    font-weight: 800;
    color: #dc0000;
}
.back_button{
    width: 4rem;
    height: .8rem;
}
</style>

<script>
import Heads from "@/components/Head";
import qrcode from "@/components/QRCode";
export default {
  name: "Code",
  components: { Heads, qrcode },
  data() {
    return {
      encryption: ''
    };
  },
  methods: {
    back(){
      this.$router.back();
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    }
  },
  mounted() {
    this.encryption = this.$route.params.encryption;
  }
};
</script>